:root {
  --clr-primary: hsl(75, 94%, 57%);
  --clr-grey: hsl(0, 0%, 20%);
  --clr-dark-grey: hsl(0, 0%, 12%);
  --clr-off-black: hsl(0, 0%, 8%);
  --clr-black: #000;
  --clr-white: #fff;
  --ff-sans: "Inter", Arial, Helvetica, sans-serif;
  --ff-mono: "Source Code Pro", monospace;
  --ff-serif: Cambria, Cochin, Georgia, serif;
  --fs-large: 1.5rem;
  --fs-reg: 0.875rem;
  --fs-small: 0.6875rem;
  --radius: 0.75rem;
  --gap: 1.5rem;
  --columns: 2;
  --transition: all 0.25s ease-in-out;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-style: normal;
  font-weight: normal;
}

body {
  font-family: var(--ff-sans);
  font-weight: 400;
  font-size: var(--fs-reg);
  line-height: 150%;
  position: relative;
  font-style: normal;
}

html,
body {
  margin: 0px auto;
  width: 100%;
  box-sizing: border-box;
  background: var(--clr-off-black);
  color: var(--clr-white);
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

/* prettier-ignore */
h1, h2 {
  font-style: normal;
  line-height: 150%;
  margin: 0;
}

h1 {
  font-weight: 500;
  font-size: var(--fs-large);
  margin-bottom: 0.25rem;
}

h2 {
  font-weight: 700;
  font-size: var(--fs-reg);
  color: var(--clr-primary);
  margin-bottom: 1.5rem;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--gap);
  width: 100%;
  max-width: 23.25rem;
  padding: 1.5rem;
  background: var(--clr-dark-grey);
  border-radius: var(--radius);
  margin: 7.25rem auto 1.25rem;
}
.card__header {
  text-align: center;
}
.card__header img {
  display: block;
  margin: auto;
  height: 5.5rem;
  width: 5.5rem;
  border-radius: 100%;
  margin-bottom: 1.5rem;
}
.card__body {
  width: 100%;
}
.card ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  text-align: center;
}
.card ul li {
  display: block;
  width: 100%;
}
.card ul li a {
  display: block;
  padding: 0.75rem;
  border-radius: 0.5rem;
  background-color: var(--clr-grey);
  color: var(--crl-white);
  text-decoration: none;
  transition: var(--transition);
}
.card ul li a:hover {
  background-color: var(--clr-primary);
  color: var(--clr-dark-grey);
}
@media (min-width: 25.75em) {
  .card {
    max-width: 24rem;
    padding: 2.5rem;
  }
}

.attribution {
  font-size: var(--fs-small);
  text-align: center;
}

.attribution a {
  color: var(--clr-primary);
}

.container {
  margin: 0 auto;
  padding: 0 1.25rem;
  width: 100%;
}
@media (min-width: 25.75em) {
  .container {
    max-width: 69.375rem;
  }
}